<template>
  <div class="container">
    <div class="container-title">
      <h2 class="page-title">Верификация</h2>
      <span class="prompt">Немного подождите</span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {

    }
  },
  async mounted() {
    await this.getCode()
  },
  methods: {
    async getCode() {
      const query = this.$route.query
      console.log(query)
      if (query.verify_code) {
        try {
          await this.$store.dispatch('user/verify', query)
        } catch(e) {
          console.log(e)
          this.$toast.error('Произошла ошибка, перейдите по ссылке еще раз')
        }
      } else {
        this.$router.push('/')
      }
    }
  }
}
</script>

<style lang="scss" scoped>

.container {
  margin: 50px auto;
}
</style>
